import React, { useCallback, useState } from "react";
import layout from "./officelayout.jpg";
import {DropFile, DisplayFile} from 'react-image-edit';


function Layout() {
    console.log(layout);
    
    return(
        <React.Fragment>
        <div className="layout">
            <img src={layout} alt="Layout of Office" height={600} width={800} />
        </div>
        
            
            
        </React.Fragment>
    );
}

export default Layout;